.cbx-guest-wrap {
  font-size: 14px;
  line-height: 1.2;

  border-radius: 8px !important;
  max-width: 315px;
  margin: 30px auto;


  .cbx-title-login{
    margin-bottom: 20px;
    a{
      text-decoration: underline !important;
    }

    .guest-login-trigger{
      text-decoration: none !important;
    }
  }

  .cbx-guest-login-wrap{
    background-color: #f0f0f1;
    border-radius: 5px;
    padding: 20px;
    margin-bottom: 20px;
    display: none;



    input[name="rememberme"]{
      width: auto !important;
      display: inline-block !important;
      vertical-align: middle !important;
    }

    input[type="submit"], button[type="submit"]{
      font-size: 15px !important;
      line-height: 1.5 !important;
      outline: none !important;
      text-decoration: none !important;
      border-radius: 6px;
      padding: 5px 15px !important;
      color: #fff !important;
      background-color: var(--color-primary) !important;
      border: 1px solid var(--color-primary) !important;
      box-shadow: 0 2px 4px rgba(0, 0, 0, .2);

      &:focus, &:hover{
        outline: none !important;
        text-decoration: none !important;
        box-shadow: none !important;

        color: #fff;
        background-color: var(--color-primary);
        border-color: var(--color-primary);
      }

      &:disabled{
        opacity: .5;
      }
    }

    input[type=color], input[type=date], input[type=datetime-local], input[type=datetime], input[type=email], input[type=month], input[type=number], input[type=password], input[type=search], input[type=tel], input[type=text], input[type=time], input[type=url], input[type=week], select, textarea{
      background-color: var(--color-white) !important;
    }

    .login-submit{
      text-align: center !important;
      margin: 20px auto 0 auto !important;
    }


    &.cbx-guest-login-wrap-show{
      display: block;
    }
  }

  form{
    p{
      margin-bottom: 2px !important;
    }
  }

  h3{
    font-weight: bold !important;
    font-size: 18px !important;
    line-height: 1.5 !important;
    padding: 0 !important;
    margin: 0 0 8px 0 !important;
    text-align: left !important;
  }

  label{
    display: block;
    width: 100% !important;
    font-size: 15px !important;
    line-height: 1.5 !important;
    padding: 0 !important;
    margin: 0 0 4px 0 !important;
  }

  .bbp-remember-me{
    label{
      display: inline-block !important;
      width: auto !important;
    }
  }

  input[type="text"], input[type="password"], input[type="date"], input[type="datetime"], input[type="datetime-local"], input[type="email"], input[type="month"], input[type="number"], input[type="search"], input[type="tel"], input[type="time"], input[type="url"], input[type="week"]{
    width: 100% !important;
    border: 1px solid #ccc !important;
    margin: 0 0 4px 0 !important;
    padding: 3px 5px !important;
    font-size: 14px !important;
    line-height: 1.2 !important;
    &:focus{
      outline: 0 none !important;
    }
  }

  .cbx-guest-register{
    padding: 0 !important;
    margin: 10px 0 0 0 !important;
  }


  .woocommerce-form.woocommerce-form-login.login{
    .form-row{
      margin: 0 !important;
    }
    .lost_password{
      margin: 5px 0;
    }
  }
}